.configtable [class^="ct-grid"] {
  display: inline-block;
  overflow: hidden;
  padding: 0.25em; }

.configtable.show-numbers .ct-header .ct-row {
  margin-left: 33px; }

.configtable.show-numbers .ct-header .ct-row-number {
  width: 33px; }

.configtable.show-numbers .ct-entry-container {
  margin: 0;
  padding: 0;
  position: relative;
  box-sizing: content-box; }

.configtable.show-numbers .ct-entries {
  margin: 0;
  padding: 0; }
  .configtable.show-numbers .ct-entries .ct-row {
    margin-left: 30px; }
  .configtable.show-numbers .ct-entries .ct-row-number {
    width: 33px;
    margin-left: -3px; }

.configtable.show-numbers .ct-row-number {
  bottom: 0;
  top: 0;
  display: block;
  font-size: 7pt;
  position: absolute;
  text-align: center; }
  .configtable.show-numbers .ct-row-number > span {
    display: block;
    height: 50%;
    position: absolute;
    top: 50%;
    transform: translateY(-25%);
    width: 100%; }

.configtable .ct-entries {
  border: 1px solid #afafaf;
  font-family: sans-serif; }

.configtable .loading {
  position: relative;
  font-size: 9pt;
  color: #fff;
  text-align: center;
  text-shadow: 0px 0px 1px #000; }
  .configtable .loading .bar {
    z-index: -1;
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    background: linear-gradient(180deg, #1f98f3, #0b57a5);
    padding: 2em; }

.configtable .ct-entry {
  background: #f3f3f3;
  background: linear-gradient(180deg, #ffffff, #f3f3f3);
  border-left: 3px solid transparent;
  outline: 1px solid #afafaf;
  transition: border-color 200ms; }
  .configtable .ct-entry:nth-child(even) {
    background: #e3e3e3;
    background: linear-gradient(180deg, #eeeeee, #e3e3e3); }
  .configtable .ct-entry input {
    border-bottom: 1px solid; }
  .configtable .ct-entry select {
    border: 1px solid; }
  .configtable .ct-entry.ct-focus {
    border-left-color: #0b57a5; }
    .configtable .ct-entry.ct-focus input:not(.ui-state-error), .configtable .ct-entry.ct-focus select:not(.ui-state-error) {
      border-color: #858585; }
  .configtable .ct-entry input, .configtable .ct-entry select {
    outline: none;
    background: none;
    border-radius: 0;
    border: 0;
    color: rgba(0, 0, 0, 0.95);
    color: #000000;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    margin: 4px;
    padding: 2px;
    transition: border 0.5s; }
    .configtable .ct-entry input.ui-state-modified, .configtable .ct-entry select.ui-state-modified {
      border-color: #0b57a5; }
    .configtable .ct-entry input.ui-state-error, .configtable .ct-entry select.ui-state-error {
      border-color: #ffc12d;
      background: radial-gradient(ellipse at bottom, rgba(255, 223, 147, 0.7), rgba(255, 193, 45, 0) 80%); }
    .configtable .ct-entry input:focus, .configtable .ct-entry select:focus {
      border-color: #1f98f3;
      background: radial-gradient(ellipse at bottom, rgba(128, 197, 248, 0.7), rgba(31, 152, 243, 0) 80%); }
  .configtable .ct-entry select.ui-state-error {
    background: radial-gradient(ellipse at bottom, rgba(255, 193, 45, 0.7), rgba(255, 255, 255, 0.5) 80%); }
  .configtable .ct-entry select:focus {
    background: radial-gradient(ellipse at bottom, rgba(128, 197, 248, 0.7), rgba(255, 255, 255, 0.5) 80%); }
  .configtable .ct-entry input:not([type="checkbox"]):not([type="radio"]),
  .configtable .ct-entry input:not([type]) {
    appearance: none;
    border-radius: 0; }
  .configtable .ct-entry input:disabled {
    opacity: 0.5; }
  .configtable .ct-entry select {
    border: 1px solid #b5b5b5;
    margin: 2px;
    padding: 2px;
    border-radius: 2px;
    background: rgba(255, 255, 255, 0.5); }
    .configtable .ct-entry select:disabled {
      border: none;
      border-bottom: 1px solid transparent;
      color: #afafaf;
      background: none; }

.configtable .ct-header .ct-row {
  border-bottom: 1px solid #afafaf; }
  .configtable .ct-header .ct-row:last-child {
    border-bottom: none; }

.configtable .ct-header-item:after {
  content: '';
  width: 0em;
  text-align: center;
  display: inline-block;
  opacity: 0;
  transition: opacity 150ms, transform 80ms, width 150ms; }

.configtable .sort-desc:after,
.configtable .sort-asc:after {
  width: 1.5em;
  color: #444;
  font-size: 7pt;
  opacity: 1;
  line-height: 100%; }

.configtable .ct-header-item {
  color: #444;
  text-shadow: 0.5px 0.5px #fff;
  border-right: 1px solid #afafaf; }
  .configtable .ct-header-item.sort-asc, .configtable .ct-header-item.sort-desc {
    background: linear-gradient(0deg, #fff 0%, #f0f0f0 70%, #c8c8c8 100%); }
  .configtable .ct-header-item:last-child {
    border-right: none; }

.configtable .dragging {
  opacity: 0.75; }

.configtable .ct-droppable {
  height: 4px;
  width: 100%;
  background: linear-gradient(90deg, transparent 0%, #1f98f3 50%, transparent 100%); }

.ct-footer,
.ct-header {
  background: linear-gradient(180deg, #f9f9f9 0em, #e0e0e0 1em);
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1); }

.ct-footer {
  text-align: right;
  border: 1px solid #afafaf;
  padding: 2px; }

.ct-row-number {
  font-family: sans-serif;
  color: #888; }

.ct-drag-and-drop .ct-entries .ct-row-number {
  user-select: none;
  cursor: move;
  background: repeating-linear-gradient(-45deg, transparent, transparent 2.5pt, rgba(0, 0, 0, 0.05) 2.5pt, rgba(0, 0, 0, 0.05) 5pt); }

.sf-float-top,
.sf-float-bottom {
  width: inherit;
  position: fixed;
  z-index: 10; }

.configtable {
  box-sizing: border-box;
  border: 1 px; }
  .configtable.fluid {
    width: auto; }
    .configtable.fluid .entry {
      width: 100%; }
  .configtable * {
    box-sizing: border-box;
    font-family: inherit; }
  .configtable .ct-header {
    margin-bottom: 2px;
    border: 1px solid #aaa;
    position: relative;
    user-select: none; }
  .configtable .ct-footer {
    margin-top: 2px; }
  .configtable .ct-entry {
    min-height: 0.5em; }
  .configtable .ct-row {
    font-size: 0;
    white-space: nowrap; }
    .configtable .ct-row > * {
      font-size: 9pt; }
  .configtable .ct-header-item {
    font-size: 8pt;
    padding: 4px;
    margin: 0;
    text-align: center;
    overflow: hidden;
    position: relative; }
    .configtable .ct-header-item.sort-desc:after {
      content: "▲";
      display: inline-block;
      transform: scaleY(1); }
    .configtable .ct-header-item.sort-asc:after {
      content: "▲";
      display: inline-block;
      transform: scaleY(-1); }
  .configtable .ct-entries {
    position: relative;
    overflow: hidden; }
    .configtable .ct-entries > * {
      position: absolute;
      width: 100%; }
  .configtable .ct-entry-container {
    overflow: hidden; }
  .configtable .ct-sizer-center {
    text-align: center; }
  .configtable .ct-sizer-expand > * {
    width: 100%; }

/* these are rules to negate the effects of configtable1 styling */
.configtable .ct-header {
  float: none; }
  .configtable .ct-header .ct-row {
    margin-top: 0px !important; }

.configtable .ct-entry {
  margin: 0px !important;
  border-top: none;
  border-right: none;
  border-bottom: none; }

.configtable .ct-row {
  float: none;
  position: relative; }

.configtable .ct-header {
  text-align: left !important; }
  .configtable .ct-header .ct-header-item {
    font-size: 8pt !important;
    float: none !important;
    border-left: none;
    border-top: none !important;
    margin: 0px !important;
    padding: 4px !important; }
  .configtable .ct-header .ct-header-item:first-child {
    float: none;
    border-left: none; }
