/* these are rules to negate the effects of configtable1 styling */

.configtable {
	.ct-header {
		float: none;

		.ct-row {
			margin-top: 0px !important;
		}
	}

	.ct-entry {
		margin: 0px !important;
		border-top: none;
		border-right: none;
		border-bottom: none;
	}
	.ct-row {
		float: none;
		position: relative;
	}

	.ct-header {
		text-align: left !important;

		.ct-header-item {
			font-size: 8pt !important;
			float: none !important;
			border-left: none;
			border-top: none !important;
			margin: 0px !important;
			padding: 4px !important;
		}

		.ct-header-item:first-child {
			float: none;
			border-left: none;
		}
	}
}
