@import 'config';
@import 'grid';
@import 'theme';
@import 'stickyfloat';

.configtable {
  box-sizing: border-box;
  border: ($table-border-px)px;

  &.fluid {
    width: auto;
    .entry {
      width: 100%;
    }
  }

  * {
    box-sizing: border-box;
    font-family: inherit;
  }

  .ct-header {
    margin-bottom: 2px;
    border: 1px solid #aaa;
    position: relative;
    user-select: none;
  }

  .ct-footer {
    margin-top: 2px;
  }

  .ct-entry {
    min-height: 0.5em;
  }

  .ct-row {
    font-size: 0;
    white-space: nowrap;
    > * {
      font-size: 9pt;
    }
  }

  .ct-header-item {
    font-size: 8pt;
    padding: 4px;
    margin: 0;
    text-align: center;
    overflow: hidden;
    position: relative;

    &.sort-desc:after {
      content: "▲";
      display: inline-block;
      transform: scaleY(1);
    }

    &.sort-asc:after {
      content: "▲";
      display: inline-block;
      transform: scaleY(-1);
    }
  }

  .ct-entries {
    position: relative;
    overflow: hidden;
    & > * {
      position: absolute;
      width: 100%;
    }
  }

  .ct-entry-container {
    overflow: hidden;
  }

  .ct-sizer-center {
    text-align: center;
  }
  .ct-sizer-expand > *{
    width: 100%;
  }
}

@import 'cf1compat';
