
.configtable {
	@for $gridCount from 1 through 4 {
		&.grid-#{$gridCount*10} {

			$gridSize: percentage(1.0 / ($gridCount*10));
			@for $i from 1 through ($gridCount*10) {
				.ct-grid-#{$i} {
					width: $gridSize * $i;
				}
			}
		}
	}
	[class^="ct-grid"] {
		display: inline-block;
		overflow: hidden;
		padding: 0.25em;
	}

	&.show-numbers {
		$numberWidth: 30px;
		$focusWidth: 3px;

			.ct-header {
				.ct-row {
					margin-left: $numberWidth + $focusWidth;
				}
				.ct-row-number {
					width: $numberWidth + $focusWidth;
				}
			}

			.ct-entry-container {
				margin: 0;
				padding: 0;
				position: relative;
				box-sizing: content-box;
			}

			.ct-entries {
				margin: 0;
				padding: 0;

				.ct-row {
					margin-left: $numberWidth;
				}

				.ct-row-number {
					$shift: 3px;
					width: $numberWidth + $shift;
					margin-left: -$shift;
				}
			}


			.ct-row-number {
				bottom: 0;
				top: 0;
				display: block;
				font-size: 7pt;
				position: absolute;
				text-align: center;

				& > span {
					display: block;
					height: 50%;
					position: absolute;
					top: 50%;
					transform: translateY(-25%);
					width: 100%;
				}
			}
	}
}
