$blue: #1f98f3;
$blue2: #0b57a5;
$yellow: #ffc12d;
$bordercolor: #afafaf;
$focuswidth: 3px;

.configtable {
  .ct-entries {
    border: 1px solid $bordercolor;
    font-family: sans-serif;
  }

  .loading {
    position: relative;
    font-size: 9pt;
    color: #fff;
    text-align: center;
    text-shadow: 0px 0px 1px #000;

    .bar {
      z-index: -1;
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      background: linear-gradient(180deg, $blue, $blue2);
      padding: 2em;
    }
  }

  .ct-entry {
    background: #f3f3f3;
    background: linear-gradient(180deg, #ffffff, #f3f3f3);
    border-left: 3px solid transparent;
    outline: 1px solid $bordercolor;

    &:nth-child(even) {
      background: #e3e3e3;
      background: linear-gradient(180deg, #eeeeee, #e3e3e3);
    }

    input {
      border-bottom: 1px solid;
    }
    select {
      border: 1px solid;
    }

    &.ct-focus {
      border-left-color: $blue2;
      input,select {
        &:not(.ui-state-error) {
          border-color: #858585;
        }
      }
    }

    input, select {
      outline: none;
      background: none;
      border-radius: 0;
      border: 0;
      color: rgba(0, 0, 0, 0.95);
      color: #000000;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);

      margin: 4px;
      padding: 2px;

      transition: border 0.5s;

      &.ui-state-modified {
        border-color: $blue2;
      }

      &.ui-state-error {
        border-color: $yellow;
        background: radial-gradient(ellipse at bottom, rgba(lighten($yellow, 20%), 0.7), rgba($yellow, 0) 80%);
      }

      &:focus {
        border-color: $blue;
        background: radial-gradient(ellipse at bottom, rgba(lighten($blue, 20%), 0.7), rgba($blue, 0) 80%);
      }
    }

    select {
      &.ui-state-error {
        background: radial-gradient(ellipse at bottom, rgba($yellow, 0.7), rgba(#fff, 0.5) 80%);
      }
      &:focus {
        background: radial-gradient(ellipse at bottom, rgba(lighten($blue, 20%), 0.7), rgba(#fff, 0.5) 80%);
      }
    }

    input:not([type="checkbox"]):not([type="radio"]),
    input:not([type]) {
      appearance: none;
      border-radius: 0;
    }

    input {
      &:disabled {
        opacity: 0.5;
      }
    }

    select {
      border: 1px solid #b5b5b5;
      margin: 2px;
      padding: 2px;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.5);

      &:disabled {
        border: none;
        border-bottom: 1px solid transparent;
        color: $bordercolor;
        background: none;
      }
    }

    transition: border-color 200ms;


  }

  .ct-header .ct-row {
    border-bottom: 1px solid $bordercolor;
    &:last-child {
      border-bottom: none;
    }
  }

  .ct-header-item:after {
    content: '';
    width: 0em;
    text-align: center;
    display: inline-block;
    opacity: 0;
    transition: opacity 150ms, transform 80ms, width 150ms;
  }

  .sort-desc:after,
  .sort-asc:after {
    width: 1.5em;
    color: #444;
    font-size: 7pt;
    opacity: 1;
    line-height: 100%;
  }

  .ct-header-item {
    color: #444;
    text-shadow: 0.5px 0.5px #fff;
    border-right: 1px solid $bordercolor;

    &.sort-asc,
    &.sort-desc {
      background: linear-gradient(0deg, #fff 0%, #f0f0f0 70%, #c8c8c8 100%);
      //border-top: none !important;
    }
    &:last-child {
      border-right: none;
    }
  }

  .dragging {
    opacity: 0.75;
  }

  .ct-droppable {
    height: 4px;
    width: 100%;
    background: linear-gradient(90deg, transparent 0%, $blue 50%, transparent 100%);
  }

  .ui-state-error {
     input, select {
    }
  }
}

.ct-footer,
.ct-header {
  background: linear-gradient(180deg, #f9f9f9 0em, #e0e0e0 1em);
  box-shadow: 0px 1px 2px rgba(0,0,0,0.1);
}

.ct-footer {
  text-align: right;
  border: 1px solid $bordercolor;
  padding: 2px;
}

.ct-row-number {
  font-family: sans-serif;
  color: #888;
}

.ct-drag-and-drop {
  .ct-entries .ct-row-number {
    $stripe: rgba(0,0,0,0.05);
    $size: 5pt;
    user-select: none;
    cursor: move;
    background: repeating-linear-gradient(-45deg, transparent, transparent ($size * 0.5), $stripe ($size * 0.5), $stripe $size);
  }
}


